/* Category Detail Start */
.brand-detail-main {
  .catg-tab-list{
    button{
      min-width: 140px;
      padding: 0 5px;
      margin-bottom: 10px;
      height: 40px;
      border-radius: 30px;
      background-color: var(--white);
      border: 1px solid var(--gray);
      color: var(--black);
      font-size: 12px;
      font-family: 'Roboto-regular';
      &:hover{
        background-color: var(--black);
        border: 1px solid var(--black);
        color: var(--white);
        box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25) !important;
      }
    }
    .active{
      background-color: var(--black);
      border: 1px solid var(--black);
      color: var(--white);
      box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25) !important;
    }
    @media screen and (max-width: 991px){
      white-space: nowrap;
      overflow-x: scroll;
    }
  }

  .select-category {
    width: 100%;
    min-width: 140px;
    max-width: 140px;
    border: 1px solid var(--gray) !important;
    cursor: pointer;
    option{
      color:var(--black) !important;
      background-color: var(--white) !important;
    }
  }
  .sort-by-count{
    font-family: 'Roboto-regular';
    font-size: 14px;
    color: var(--black);
    background-color: var(--white);
    background-image: url("./imgs/select-drop.svg");
    background-repeat: no-repeat;
    background-position: 90%;
    -webkit-appearance: none;
    width: auto;
    padding: 10px 30px 10px 15px;
    border-radius: 25px;
    border: none;
    outline: none;
    cursor: pointer;
  }

  .sort-by-brand{
    font-family: 'Roboto-regular';
    font-size: 13px;
    color: var(--black);
    background-color: var(--white);
    background-image: url("./imgs/select-drop.svg");
    background-repeat: no-repeat;
    background-position: 90%;
    -webkit-appearance: none;
    width: 170px;
    padding: 10px 30px 10px 15px;
    border-radius: 25px;
    border: none;
    outline: none;
    cursor: pointer;
    @media screen and (max-width: 575px){
      width: 120px;
    }
  }
  .brand-banner {
    position: relative;
    max-height: 250px;
    border-radius: 25px;
    background-color: var(--white);
    @media screen and (max-width: 575px){
      height: 331px;
    }
    @media screen and (max-width: 375px){
      height: 290px;
      margin-top: 15px;
    }
    img{
      border-radius: 25px;
      z-index: 0;
      max-height: 250px;
    }
    .brand-banner-comp {
      position: absolute;
      left: 25px;
      bottom: -25px;
      width: 150px;
      height: 75px;
      background-color: var(--white);
      display: flex;
      align-items: center;
      justify-content: center;
      border: 1px solid rgba(0, 0, 0, 0.1);
      z-index: 2;
      img{
        width: 100%;
        object-fit: contain;
        height: 60px;

      }
    }
    .brand-banner-text {
      position: absolute;
      left: 25px;
      bottom: 10px;
      z-index: 2;
    }
    .brand-banner-overlay{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
     // background: rgba(0,0,0,0.5);
      background: linear-gradient(3deg, rgba(0,0,0,1) 0%, rgba(235,255,255,0.3) 100%);
      z-index: 1;
      border-radius: 25px;
    }
  }
  .brand-filter-sidebar {
    border: 1px solid var(--gray);

    .card {
      border: none;

      .card-header {
        cursor: pointer;
        padding: 15px;
        border-bottom: 1px solid var(--gray);
      }

      .filter-btn {
        background-color: var(--yellow);
        width: 100%;
        height: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
        border: none;
        border-radius: 50px;
      }
    }

    .collapse-list-item {
      max-height: 300px;
      overflow-y: auto;
      li {
        padding: 10px 15px;
        border-bottom: 1px solid var(--gray);
      }
    }
  }

  .filter-applied-main {
    border: 1px solid var(--gray);

    .filter-head {
      padding: 15px;
    }

    .applied-filter-chip {
      border-top: 1px solid var(--gray);
      .chip-main{
        border: 1px solid var(--gray);
        border-radius: 5px;
        padding: 5px 25px 5px 5px;
        margin-bottom: 10px;
        margin-right: 10px;
        cursor: pointer;
        span {
          font-family: 'Roboto-regular';
          font-size: 13px;
          display: inline-block;
          color: var(--light-gray);
        }

        .close-chip{
          position: absolute;
          right: 5px;
          top: 5px;
        }
      }
    }
  }

  /* The check-main */
  .check-main {
    display: block;
    position: relative;
    cursor: pointer;
    margin-bottom: 0;
    font-size: 13px;
    font-family: 'Roboto-regular';
    color: var(--light-gray);
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }

  .sub-lable {
    color: var(--dark-gray);
    font-size: 12px;
  }

  /* Hide the browser's default checkbox */
  .check-main input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
  }

  /* Create a custom checkbox */
  .checkmark {
    position: absolute;
    top: 2px;
    right: 0;
    left: auto;
    height: 16px;
    width: 16px;
    border-radius: 3px;
    border: 1px solid var(--dark-gray);
    background-color: #ffffff;
    box-shadow: 0px 6px 25px #00000008;
    .brand-image {
      width: 120px;
      height: 90px;
    }
  }

  /* When the checkbox is checked, add a blue background */
  .check-main input:checked ~ .checkmark {
    border: 1px solid var(--yellow);
    background-color: var(--yellow);
  }

  /* Create the checkmark/indicator (hidden when not checked) */
  .checkmark:after {
    content: "";
    position: absolute;
    display: none;
  }

  /* Show the checkmark when checked */
  .check-main input:checked ~ .checkmark:after {
    display: block;
  }

  /* Style the checkmark/indicator */
  .check-main .checkmark:after {
    left: 5px;
    top: 0;
    width: 5px;
    height: 10px;
    border: solid white;
    border-width: 0 2px 2px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
  }


  /* The check-main-radio */
  .check-main-radio {
    display: block;
    position: relative;
    cursor: pointer;
    margin-bottom: 0;
    font-size: 13px;
    font-family: 'Roboto-regular';
    color: var(--light-gray);
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }

  /* Hide the browser's default checkbox */
  .check-main-radio input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
  }

  /* Create a custom checkbox */
  .checkmark-radio {
    position: absolute;
    top: 2px;
    right: 0;
    height: 16px;
    width: 16px;
    border-radius: 50%;
    border: 1px solid var(--dark-gray);
    background-color: #ffffff;
    box-shadow: 0px 6px 25px #00000008;
    .brand-image {
      width: 120px;
      height: 90px;
    }
  }

  /* When the checkbox is checked, add a blue background */
  .check-main-radio input:checked ~ .checkmark-radio {
    border: 1px solid var(--yellow);
    background-color: var(--yellow);
  }

  /* Create the checkmark/indicator (hidden when not checked) */
  .checkmark-radio:after {
    content: "";
    position: absolute;
    display: none;
  }

  /* Show the checkmark when checked */
  .check-main-radio input:checked ~ .checkmark-radio:after {
    display: block;
  }

  /* Style the checkmark/indicator */
  .check-main-radio .checkmark-radio:after {
    left: 4px;
    top: 4px;
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background-color: var(--white);
  }

  .mobile-filter{
    position: fixed;
    z-index: 2;
    bottom: -1px;
    left: 0;
    width: 100%;
    border-radius: 20px 20px 0 0;
    background-color: #6c757d;
    .filter-main{
      div{
        border-radius: 50px;
      }
    }
  }
}

.sub-spinner {
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.3);
  bottom: 0;
  left: 0;
  right: 0;
  top: 0;
  z-index: 1;
  align-items: center;
  justify-content: center;
  display: flex;
}

.main-heading:after {
  /* symbol for "opening" panels */
  font-family:'FontAwesome';
  content:"\f078"; //DOwn
  float: right;
  color: var(--yellow);
}
.panel-heading.collapsed .main-heading:after {
  /* symbol for "collapsed" panels */
  content:"\f054" !important; // Right
  color: inherit;
}

.category-heading {
  display: flex;
  justify-content: space-between;
  padding: 10px 15px;
  border-bottom: 1px solid var(--gray);
  vertical-align: top;
  cursor: pointer;
}
/* Brand Detail End */

/*Mobile Responsive Content Start*/
.active{
  transform: translateY(0) !important;
  transition: transform 225ms cubic-bezier(0,0,.2,1) 0s !important;
  -webkit-transition: -webkit-transform 225ms cubic-bezier(0,0,.2,1) 0s !important;
}

.sortby-modal{
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  height: auto;
  max-height: 100%;
  -webkit-transform: translateY(100%);
  transform: translateY(100%);
  -webkit-transition: -webkit-transform 225ms cubic-bezier(0,0,.2,1) 0s;
  transition: -webkit-transform 225ms cubic-bezier(0,0,.2,1) 0s;
  .modal-content{
    bottom: 0;
    border-radius: 15px 15px 0 0;
    background-color: #fff;
    overflow-y: auto;
    scroll-behavior: smooth;
  }
}

.mobile-filter-tabination{
  height: calc(100vh - 75px);
  background-color: #fffdfd;
 .nav{
   border-right: 1px solid var(--gray);
   .active{
     border-right: 2px solid var(--yellow);
     background: transparent;
     color: var(--light-gray);
   }
   a{
     background: transparent;
     padding: 15px 0 15px 15px;
     font-size: 14px;
     font-family: 'Roboto-regular';
     color: var(--light-gray);
     border-right: 2px solid var(--white);
     border-bottom: 1px solid var(--gray);
     display: flex;
     justify-content: space-between;
     align-items: center;
   }
 }


  .apply-filter-btn-mobile{
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    z-index: 1;
    button{
      border-radius: 40px;
    }

  }

  .mobile-filter-tabination-content{
    li{
      padding: 15px 0;
      border-bottom: 1px solid var(--gray);
    }
  }
}

/*Mobile Responsive Content End*/

.filter-slider-sticky{
  position: sticky;
  top: 140px;
  height: 100%;
}

.scrollable-filers {
  height: calc(100vh - 65px);
  overflow: auto;
  padding-bottom: 100px;
  &::-webkit-scrollbar {
    width: 0;
  }
}
.product-description p{
    word-break: break-word;
}
